import React, { useState, useRef, useEffect, useCallback } from "react";
import { YINPitchDetector, PitchResult } from '../utils/pitchAnalysis';
import { AudioPlaybackController } from '../utils/audioUtils';
import { useAudioPlaybackSync } from './useAudioPlaybackSync';

interface AudioRecordingState {
  isRecording: boolean;
  audioStream: MediaStream | null;
  audioContext: AudioContext | null;
  analyser: AnalyserNode | null;
  error: string | null;
  recordedBlob: Blob | null;
  isPlayingRecorded: boolean;
  //      
  advancedPitchData: PitchResult[];
  currentPitchConfidence: number;
  //    
  autoProcessResult: any | null;
}

export const useAudioRecording = (
  learnerInfo?: {name: string, gender: string, ageGroup: string}, 
  selectedFile?: string,
  chartInstance?: { updatePlaybackProgress?: (time: number) => void; clearPlaybackProgress?: () => void }
) => {
  const [state, setState] = useState<AudioRecordingState>({
    isRecording: false,
    audioStream: null,
    audioContext: null,
    analyser: null,
    error: null,
    recordedBlob: null,
    isPlayingRecorded: false,
    //      
    advancedPitchData: [],
    currentPitchConfidence: 0,
    //    
    autoProcessResult: null,
  });

  const animationFrameRef = useRef<number | undefined>(undefined);
  const onPitchDataRef = useRef<
    ((frequency: number, timestamp: number) => void) | null
  >(null);
  const mediaRecorderRef = useRef<MediaRecorder | null>(null);
  const audioChunksRef = useRef<Blob[]>([]);
  const recordedAudioRef = useRef<HTMLAudioElement | null>(null);
  
  //      ref
  const learnerInfoRef = useRef(learnerInfo);
  const selectedFileRef = useRef(selectedFile);
  
  //      
  const yinDetectorRef = useRef<YINPitchDetector | null>(null);
  const audioPlaybackRef = useRef<AudioPlaybackController>(new AudioPlaybackController());
  
  //      
  const recordedPlaybackSync = useAudioPlaybackSync({
    chartInstance,
    updateInterval: 'frame',
    enableLogging: true
  });
  
  //  ref  
  useEffect(() => {
    learnerInfoRef.current = learnerInfo;
    selectedFileRef.current = selectedFile;
  }, [learnerInfo, selectedFile]);

  //     
  useEffect(() => {
    console.log(' [STEP 3] Hook   :', {
      'state.isPlayingRecorded': state.isPlayingRecorded,
      'state.recordedBlob': !!state.recordedBlob,
      'recordedAudioRef.current': !!recordedAudioRef.current
    });
  }, [state.isPlayingRecorded, state.recordedBlob]);

  const startRecording = useCallback(async () => {
    try {
      //        ( ref  )
      const currentLearnerInfo = learnerInfoRef.current;
      const currentSelectedFile = selectedFileRef.current;
      
      if (!currentLearnerInfo || !currentLearnerInfo.name || !currentLearnerInfo.gender) {
        alert("    !\n\n-     .");
        return;
      }
      
      if (!currentSelectedFile) {
        alert("   !\n\n- 10       .");
        return;
      }
      
      console.log("      -  ");
      console.log(" :", `${currentLearnerInfo.name} (${currentLearnerInfo.gender}, ${currentLearnerInfo.ageGroup || ' '})`);
      console.log(" :", currentSelectedFile);
      
      const stream = await navigator.mediaDevices.getUserMedia({
        audio: {
          echoCancellation: false,
          noiseSuppression: false,
          autoGainControl: false,
          sampleRate: 44100,
        },
      });

      const audioContext = new AudioContext({ sampleRate: 44100 });
      const analyser = audioContext.createAnalyser();
      const source = audioContext.createMediaStreamSource(stream);

      analyser.fftSize = 4096;
      analyser.smoothingTimeConstant = 0.3;
      source.connect(analyser);
      
      //   YIN   
      yinDetectorRef.current = new YINPitchDetector(audioContext.sampleRate, {
        frameMs: 25,
        confidenceThreshold: 0.6,
        voicingThreshold: 0.45
      });

      // Setup MediaRecorder for file saving
      const mediaRecorder = new MediaRecorder(stream, {
        mimeType: "audio/webm;codecs=opus",
      });

      audioChunksRef.current = [];

      mediaRecorder.ondataavailable = (event) => {
        if (event.data.size > 0) {
          audioChunksRef.current.push(event.data);
        }
      };

      mediaRecorder.onstop = async () => {
        const audioBlob = new Blob(audioChunksRef.current, {
          type: "audio/webm",
        });

        setState((prev) => ({
          ...prev,
          recordedBlob: audioBlob,
          //     
          advancedPitchData: [],
          currentPitchConfidence: 0,
        }));

        // Upload to backend
        await uploadRecordedAudio(audioBlob);
      };

      mediaRecorderRef.current = mediaRecorder;
      mediaRecorder.start(1000); // Record in 1-second chunks

      setState((prev) => ({
        ...prev,
        isRecording: true,
        audioStream: stream,
        audioContext,
        analyser,
        error: null,
      }));

      // Start pitch detection
      const detectPitch = () => {
        if (!analyser) return;

        const bufferLength = analyser.frequencyBinCount;
        const dataArray = new Float32Array(bufferLength);
        analyser.getFloatFrequencyData(dataArray);

        // Simple pitch detection using autocorrelation
        const sampleRate = audioContext.sampleRate;
        const timeDomainData = new Float32Array(analyser.fftSize);
        analyser.getFloatTimeDomainData(timeDomainData);

        const frequency = autoCorrelate(timeDomainData, sampleRate);

        if (frequency > 0 && onPitchDataRef.current) {
          onPitchDataRef.current(frequency, Date.now());
        }

        animationFrameRef.current = requestAnimationFrame(detectPitch);
      };

      detectPitch();
    } catch (error) {
      setState((prev) => ({
        ...prev,
        error: `  : ${error instanceof Error ? error.message : "   "}`,
      }));
    }
  }, [learnerInfo, selectedFile]);

  const stopRecording = useCallback(() => {
    if (animationFrameRef.current) {
      cancelAnimationFrame(animationFrameRef.current);
    }

    if (
      mediaRecorderRef.current &&
      mediaRecorderRef.current.state !== "inactive"
    ) {
      mediaRecorderRef.current.stop();
    }

    if (state.audioStream) {
      state.audioStream.getTracks().forEach((track) => track.stop());
    }

    if (state.audioContext) {
      state.audioContext.close();
    }

    setState((prev) => ({
      ...prev,
      isRecording: false,
      audioStream: null,
      audioContext: null,
      analyser: null,
      error: null,
      recordedBlob: null,
    }));
  }, [state, learnerInfo, selectedFile]);

  const uploadRecordedAudio = async (audioBlob: Blob) => {
    try {
      //   ref  
      const currentLearnerInfo = learnerInfoRef.current;
      const currentSelectedFile = selectedFileRef.current;
      
      console.log("   -   ...");
      console.log(" :", `${currentLearnerInfo?.name} (${currentLearnerInfo?.gender}, ${currentLearnerInfo?.ageGroup})`);
      console.log(" :", currentSelectedFile);
      
      const formData = new FormData();
      formData.append("file", audioBlob, "recording.webm");
      formData.append("sentence_hint", "");
      formData.append("save_permanent", "true");
      
      //   
      if (currentLearnerInfo) {
        formData.append("learner_name", currentLearnerInfo.name || "");
        formData.append("learner_gender", currentLearnerInfo.gender || "");
        formData.append("learner_age_group", currentLearnerInfo.ageGroup || "");
      }
      
      //  
      if (currentSelectedFile) {
        formData.append("reference_sentence", currentSelectedFile);
      }

      const response = await fetch("/api/auto-process", {
        method: "POST",
        body: formData,
      });

      if (response.ok) {
        const result = await response.json();
        console.log("   :", result);
        
        //    
        setState((prev) => ({
          ...prev,
          autoProcessResult: result,
        }));
        
        if (result.success) {
          console.log(` STT : "${result.transcription}"`);
          console.log(` ${result.syllables?.length || 0}   `);
        }
      } else {
        console.error("   :", response.statusText);
      }
    } catch (error) {
      console.error("    :", error);
    }
  };

  const playRecordedAudio = useCallback(async () => {
    console.log(' [STEP 2] playRecordedAudio  !');
    
    //    
    console.log(' [STEP 2.1]  Hook  :', {
      'state.recordedBlob': !!state.recordedBlob,
      'state.isPlayingRecorded': state.isPlayingRecorded,
      'state.isRecording': state.isRecording,
      'audioPlaybackController': !!audioPlaybackRef.current
    });
    
    //   
    if (!state.recordedBlob) {
      console.log(" [STEP 2.2]    -  ");
      return;
    }
    console.log(" [STEP 2.2]    ");

    //     ( AudioPlaybackController )
    if (state.isPlayingRecorded) {
      console.log(" [STEP 2.3]    -   ");
      
      audioPlaybackRef.current.stop();
      
      console.log(" [STEP 2.3.4] setState isPlayingRecorded: false ");
      setState(prev => {
        console.log(" [STEP 2.3.5] setState   -  :", prev.isPlayingRecorded);
        return { ...prev, isPlayingRecorded: false };
      });
      
      console.log(" [STEP 2.3.6]    -  ");
      return;
    }
    console.log(" [STEP 2.3]     -    ");

    //        
    console.log(" [STEP 2.4]      ");
    try {
      // Blob Audio  
      const audioUrl = URL.createObjectURL(state.recordedBlob);
      const audio = new Audio(audioUrl);
      
      //      
      const cleanup = recordedPlaybackSync.setupAudioElement(audio);
      
      //    
      audio.addEventListener('ended', () => {
        console.log(" [EVENT]    ");
        setState(prev => ({ ...prev, isPlayingRecorded: false }));
        cleanup(); // 
        URL.revokeObjectURL(audioUrl); //  
      });
      
      audio.addEventListener('error', (error) => {
        console.error(" [EVENT]    :", error);
        setState(prev => ({ ...prev, isPlayingRecorded: false }));
        cleanup(); // 
        URL.revokeObjectURL(audioUrl); //  
      });
      
      //  
      await audio.play();
      setState(prev => ({ ...prev, isPlayingRecorded: true }));
      console.log(" [STEP 2.4]      ");

    } catch (error) {
      console.error(" [STEP 2.4]     :", error);
      setState(prev => ({ ...prev, isPlayingRecorded: false }));
    }
    
    console.log(' [STEP 2] playRecordedAudio  ');
  }, [state.recordedBlob, state.isPlayingRecorded, recordedPlaybackSync]);

  const setPitchCallback = useCallback(
    (callback: (frequency: number, timestamp: number) => void) => {
      onPitchDataRef.current = callback;
    },
    [],
  );

  return {
    ...state,
    startRecording,
    stopRecording,
    playRecordedAudio,
    setPitchCallback,
  };
};

// Autocorrelation pitch detection algorithm
function autoCorrelate(buffer: Float32Array, sampleRate: number): number {
  const SIZE = buffer.length;
  const MAX_SAMPLES = Math.floor(SIZE / 2);
  let bestOffset = -1;
  let bestCorrelation = 0;
  let rms = 0;
  let foundGoodCorrelation = false;
  const correlations = new Array(MAX_SAMPLES);

  for (let i = 0; i < SIZE; i++) {
    const val = buffer[i];
    rms += val * val;
  }
  rms = Math.sqrt(rms / SIZE);

  if (rms < 0.01) return -1;

  let lastCorrelation = 1;
  for (let offset = 1; offset < MAX_SAMPLES; offset++) {
    let correlation = 0;
    for (let i = 0; i < MAX_SAMPLES; i++) {
      correlation += Math.abs(buffer[i] - buffer[i + offset]);
    }
    correlation = 1 - correlation / MAX_SAMPLES;
    correlations[offset] = correlation;

    if (correlation > 0.9 && correlation > lastCorrelation) {
      foundGoodCorrelation = true;
      if (correlation > bestCorrelation) {
        bestCorrelation = correlation;
        bestOffset = offset;
      }
    } else if (foundGoodCorrelation) {
      const shift =
        (correlations[bestOffset + 1] - correlations[bestOffset - 1]) /
        correlations[bestOffset];
      return sampleRate / (bestOffset + 8 * shift);
    }
    lastCorrelation = correlation;
  }

  if (bestCorrelation > 0.01) {
    return sampleRate / bestOffset;
  }
  return -1;
}
