import React, { useState, useEffect, useRef } from 'react';
import { useDualAxisChart } from '../hooks/useDualAxisChart';
import { tonebridgeApi, UploadedFile } from '../utils/tonebridgeApi';

// UploadedFile  tonebridgeApi import

interface SyllablePoint {
  syllable: string;
  start: number;
  end: number;
  frequency: number;
  time: number; //  
}

const UploadedFileTestSection: React.FC = () => {
  const [uploadedFiles, setUploadedFiles] = useState<UploadedFile[]>([]);
  const [selectedFileId, setSelectedFileId] = useState<string>('');
  const [loading, setLoading] = useState<boolean>(false);
  const [error, setError] = useState<string>('');
  const [syllablePoints, setSyllablePoints] = useState<SyllablePoint[]>([]);
  const [currentPlayingSyllable, setCurrentPlayingSyllable] = useState<number>(-1);
  const [showDeleteConfirm, setShowDeleteConfirm] = useState(false);
  const [fileToDelete, setFileToDelete] = useState<string>('');
  
  const chartCanvasRef = useRef<HTMLCanvasElement>(null);
  const audioRef = useRef<HTMLAudioElement>(null);
  const testDualAxisChart = useDualAxisChart(chartCanvasRef, 'uploaded-file-test');

  //      
  useEffect(() => {
    console.log('      ');
    console.log('   :   ');
  }, [testDualAxisChart]);

  //     ( )
  useEffect(() => {
    loadUploadedFiles();
  }, []); //  dependency array   

  const loadUploadedFiles = async () => {
    try {
      setLoading(true);
      const response = await tonebridgeApi.uploadedFiles.getList();
      
      if (!response.success) {
        throw new Error(response.error || '   ');
      }
      
      //     (TextGrid , _original/_trimmed  )
      const processedFiles = (response.data || []).filter((file: UploadedFile) => {
        // TextGrid  
        if (!file.has_textgrid) return false;
        
        // _original, _trimmed    (  )
        if (file.file_id.endsWith('_original') || file.file_id.endsWith('_trimmed')) {
          return false;
        }
        
        return true;
      });
      
      setUploadedFiles(processedFiles);
    } catch (err) {
      setError(err instanceof Error ? err.message : '   ');
    } finally {
      setLoading(false);
    }
  };

  //    
  const handleManualReprocess = async (fileId: string) => {
    if (!fileId) return;
    
    try {
      setLoading(true);
      const response = await tonebridgeApi.uploadedFiles.optimize(fileId);
      
      if (!response.success) {
        throw new Error(response.error || ' API  ');
      }
      
      //       
      await loadUploadedFiles();
      
      //     
      if (selectedFileId === fileId) {
        await handleFileSelect(fileId);
      }
      
    } catch (err) {
      setError(err instanceof Error ? err.message : '  ');
    } finally {
      setLoading(false);
    }
  };

  //    
  const handleDeleteClick = (fileId: string) => {
    setFileToDelete(fileId);
    setShowDeleteConfirm(true);
  };

  //   
  const handleDeleteConfirm = async () => {
    if (!fileToDelete) return;
    
    console.log(` Delete requested for file: ${fileToDelete}`);
    setLoading(true);
    setError('');
    setShowDeleteConfirm(false);
    
    try {
      const response = await tonebridgeApi.uploadedFiles.delete(fileToDelete);
      
      if (response.success) {
        console.log(' File deleted successfully:', response.data);
        setError('');
        
        //       
        if (selectedFileId === fileToDelete) {
          setSelectedFileId('');
        }
        
        //      
        await loadUploadedFiles();
      } else {
        throw new Error(response.error || 'File deletion failed');
      }
    } catch (err) {
      console.error(' File deletion error:', err);
      setError(err instanceof Error ? err.message : 'File deletion failed');
    } finally {
      setLoading(false);
      setFileToDelete('');
    }
  };

  //  
  const handleDeleteCancel = () => {
    setShowDeleteConfirm(false);
    setFileToDelete('');
  };


  //     (  )
  const handleSyllableClick = (syllableIndex: number) => {
    const syllable = syllablePoints[syllableIndex];
    if (syllable && audioRef.current) {
      console.log(`  : ${syllable.syllable} (${syllable.start}s - ${syllable.end}s)`);
      audioRef.current.currentTime = syllable.start;
      audioRef.current.play();
      setCurrentPlayingSyllable(syllableIndex);
    }
  };

  //      (  )
  const handleFileSelect = async (fileId: string) => {
    console.log(' handleFileSelect  ', fileId);
    
    if (!fileId) {
      console.log('   ');
      setSelectedFileId('');
      setSyllablePoints([]);
      setCurrentPlayingSyllable(-1);
      try {
        testDualAxisChart.clearChart();
      } catch (err) {
        console.warn('    ( ):', err);
      }
      return;
    }

    try {
      console.log('    :', fileId);
      setLoading(true);
      setSelectedFileId(fileId);
      setError('');
      
      //    
      if (!testDualAxisChart) {
        throw new Error('  ');
      }

      console.log(`    : ${fileId} `);

      //   :      
      console.log(`    : ${fileId}`);
      
      //       
      const selectedFile = uploadedFiles.find(f => f.file_id === fileId);
      const hasTextGrid = selectedFile?.has_textgrid;
      
      if (hasTextGrid) {
        console.log(`    : ${fileId} ( )`);
      } else {
        console.log(`   : ${fileId} ( )`);
        const formData = new FormData();
        formData.append('file_id', fileId);
        
        const optimizeResponse = await fetch(`/api/optimize-uploaded-file`, {
          method: 'POST',
          body: formData
        });

        if (optimizeResponse.ok) {
          const optimizeResult = await optimizeResponse.json();
          console.log(`  : ${optimizeResult.syllables?.length || 0} `);
          
          //      
          loadUploadedFiles();
        } else {
          console.warn(`  ,   `);
        }
      }

      //  :  API    
      // syllable_only=true      
      const pitchResponse = await tonebridgeApi.uploadedFiles.getPitchData(fileId);
      
      if (!pitchResponse.success) {
        throw new Error(pitchResponse.error || '    ');
      }
      
      const syllablePitch = pitchResponse.data || [];

      //      (syllablePitch    )
      
      const points: SyllablePoint[] = [];
      
      //  syllablePitch     
      if (syllablePitch.length > 0) {
        syllablePitch.forEach((sp: any, index: number) => {
          if (typeof sp.frequency === 'number' && sp.syllable) {
            // start/end  ,    
            const startTime = sp.start !== undefined ? sp.start : sp.time - 0.1;
            const endTime = sp.end !== undefined ? sp.end : sp.time + 0.1;
            points.push({
              syllable: sp.syllable,
              start: startTime,
              end: endTime,
              frequency: sp.frequency,
              time: sp.time
            });
            //  :   
            if (index === 0 || index === syllablePitch.length - 1) {
              console.log(`  ${index + 1}: "${sp.syllable}" [${startTime}s - ${endTime}s] ${sp.frequency.toFixed(1)}Hz`);
            }
          } else {
            console.warn(`     [${index}]:`, sp);
          }
        });
      } else {
        console.warn('    ');
      }

      setSyllablePoints(points);
      console.log(`    : ${points.length} `);

      //     
      testDualAxisChart.clearChart();
      
      //      
      syllablePitch.forEach((point: any) => {
        if (point && typeof point.frequency === 'number' && typeof point.time === 'number') {
          testDualAxisChart.addDualAxisData(point.frequency, point.time, 'reference');
        }
      });

      // 7.  annotation 
      try {
        if (points.length > 0) {
          const annotationData = points.map((point) => ({
            label: point.syllable,
            start: point.start,
            end: point.end,
            frequency: point.frequency,
            time: point.time
          }));
          
          console.log(`    annotation : ${annotationData.length}`);
          testDualAxisChart.addSyllableAnnotations(annotationData);
          console.log('  annotation  ');
        } else {
          console.log('    annotation  ');
        }
      } catch (annotationError) {
        console.error('  annotation  :', annotationError);
        // annotation     
        console.warn(' annotation    ');
      }

      console.log(`    : ${syllablePitch.length}  , ${points.length} `);
      
    } catch (err) {
      setError(err instanceof Error ? err.message : '  ');
      console.error('    :', err);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div>
      {/*    */}
      <div className="row mb-4">
        <div className="col-md-8">
          <label htmlFor="uploadedFileSelect" className="form-label">
            <i className="fas fa-file-audio me-2"></i>  
          </label>
          <select
            id="uploadedFileSelect"
            className="form-select"
            value={selectedFileId}
            onChange={(e) => handleFileSelect(e.target.value)}
            disabled={false}
          >
            <option value=""> </option>
            {uploadedFiles.map((file) => {
              //      
              const parts = file.file_id.split('_');
              const learnerName = parts[0] || '';
              const gender = parts[1] || '';
              const ageGroup = parts[2] || '';
              const sentence = file.expected_text || parts[3] || '';
              const timestamp = parts[4] || '';
              const displayName = `${learnerName} (${gender}, ${ageGroup}) - ${sentence}`;
              const processingStatus = file.has_textgrid ? '' : '';
              
              return (
                <option key={file.file_id} value={file.file_id}>
                  {processingStatus} {displayName} {timestamp && `(${timestamp})`}
                </option>
              );
            })}
          </select>
          {uploadedFiles.length === 0 && !loading && (
            <div className="text-muted small mt-1">
              <i className="fas fa-info-circle me-1"></i>
                 .      !
            </div>
          )}
        </div>
        <div className="col-md-4 d-flex align-items-end gap-2">
          <button
            className="btn btn-outline-success"
            onClick={loadUploadedFiles}
            disabled={loading}
          >
            <i className="fas fa-sync-alt me-2"></i>
          </button>
          
          {/*    */}
          {selectedFileId && (
            <button
              className="btn btn-outline-warning"
              onClick={() => handleManualReprocess(selectedFileId)}
              disabled={loading}
              title="   "
            >
              <i className="fas fa-redo me-2"></i>
            </button>
          )}
          
          {/*    */}
          {selectedFileId && (
            <button
              className="btn btn-outline-danger"
              onClick={() => handleDeleteClick(selectedFileId)}
              disabled={loading}
              title="   "
            >
              <i className="fas fa-trash me-2"></i>
            </button>
          )}
        </div>
      </div>

      {/*   */}
      {error && (
        <div className="alert alert-danger" role="alert">
          <i className="fas fa-exclamation-triangle me-2"></i>
          {error}
        </div>
      )}

      {/*   */}
      {loading && selectedFileId && (
        <div className="text-center text-muted">
          <i className="fas fa-spinner fa-spin me-2"></i>
            ... ({selectedFileId})
        </div>
      )}

      {/*    */}
      {selectedFileId && (
        <div className="alert alert-info mb-4">
          <div className="row">
            <div className="col-md-6">
              <h6 className="mb-1">
                <i className="fas fa-file-audio me-2"></i>
                 : {(() => {
                  const file = uploadedFiles.find(f => f.file_id === selectedFileId);
                  if (!file) return selectedFileId;
                  const parts = file.file_id.split('_');
                  const learnerName = parts[0] || '';
                  const sentence = file.expected_text || parts[3] || '';
                  return `${learnerName} - ${sentence}`;
                })()}
              </h6>
              <small className="text-muted">
                : {selectedFileId}.wav {uploadedFiles.find(f => f.file_id === selectedFileId)?.has_textgrid ? '/ TextGrid ' : '/ TextGrid '}
              </small>
              <br />
              <small className="text-muted">
                 : {(() => {
                  const file = uploadedFiles.find(f => f.file_id === selectedFileId);
                  if (!file) return '';
                  return new Date(file.modified_time * 1000).toLocaleString();
                })()}
              </small>
            </div>
            <div className="col-md-6">
              <div className="mt-2">
                <label className="form-label mb-1">
                  <i className="fas fa-play me-2"></i>  
                </label>
                <audio
                  ref={audioRef}
                  controls
                  className="w-100"
                  style={{ height: '35px' }}
                  src={`/static/uploads/${selectedFileId}.wav`}
                  onError={() => console.error('  :', selectedFileId)}
                  onLoadedData={() => console.log('  :', selectedFileId)}
                  onPlay={() => {
                    console.log('    ');
                    //    
                    const updateProgress = () => {
                      if (audioRef.current && !audioRef.current.paused) {
                        const currentTime = audioRef.current.currentTime;
                        
                        //      
                        testDualAxisChart.updatePlaybackProgress(currentTime);
                        
                        //     
                        const currentSyllableIndex = syllablePoints.findIndex(
                          point => currentTime >= point.start && currentTime <= point.end
                        );
                        setCurrentPlayingSyllable(currentSyllableIndex);
                        requestAnimationFrame(updateProgress);
                      }
                    };
                    requestAnimationFrame(updateProgress);
                  }}
                  onPause={() => {
                    console.log('    ');
                    testDualAxisChart.clearPlaybackProgress();
                    setCurrentPlayingSyllable(-1);
                  }}
                  onEnded={() => {
                    console.log('    ');
                    testDualAxisChart.clearPlaybackProgress();
                    setCurrentPlayingSyllable(-1);
                  }}
                >
                      .
                </audio>
              </div>
            </div>
          </div>
        </div>
      )}

      {/*   */}
      <div className="position-relative" style={{ height: '400px' }}>
        <canvas
          ref={chartCanvasRef}
          className="border rounded"
          style={{ width: '100%', height: '100%' }}
        />
        {!selectedFileId && (
          <div className="position-absolute top-50 start-50 translate-middle text-center text-muted">
            <i className="fas fa-chart-line fa-3x mb-3 opacity-50"></i>
            <div>    </div>
          </div>
        )}
      </div>

      {/*    */}
      {selectedFileId && (
        <div className="mt-3">
          <div className="row align-items-center">
            <div className="col-md-6">
              <h6 className="mb-2">
                <i className="fas fa-cog me-2"></i> 
              </h6>
              <div className="btn-group btn-group-sm" role="group">
                <button
                  className={`btn ${testDualAxisChart.yAxisUnit === 'semitone' ? 'btn-primary' : 'btn-outline-primary'}`}
                  onClick={() => testDualAxisChart.setYAxisUnit('semitone')}
                  title="  "
                >
                  <i className="fas fa-music me-1"></i>
                </button>
                <button
                  className={`btn ${testDualAxisChart.yAxisUnit === 'qtone' ? 'btn-primary' : 'btn-outline-primary'}`}
                  onClick={() => testDualAxisChart.setYAxisUnit('qtone')}
                  title="  "
                >
                  <i className="fas fa-adjust me-1"></i>
                </button>
              </div>
            </div>
            <div className="col-md-6 text-end">
              <small className="text-muted">
                <i className="fas fa-info-circle me-1"></i>
                 Y   
              </small>
            </div>
          </div>
        </div>
      )}

      {/*     */}
      {(() => {
        console.log(`   : selectedFileId=${selectedFileId}, syllablePoints.length=${syllablePoints.length} `);
        console.log(' syllablePoints :', syllablePoints);
        return null;
      })()}
      {selectedFileId && syllablePoints.length > 0 && (
        <div className="mt-3">
          <h6 className="mb-3">
            <i className="fas fa-table me-2"></i>   
          </h6>
          <div className="table-responsive">
            <table className="table table-sm table-bordered table-hover">
              <thead className="table-light">
                <tr>
                  <th style={{width: '6%'}} className="text-center"></th>
                  <th style={{width: '10%'}} className="text-center">F0 (Hz)</th>
                  <th style={{width: '10%'}} className="text-center">
                    {testDualAxisChart.yAxisUnit === 'semitone' ? '' : ''}
                  </th>
                  <th style={{width: '12%'}} className="text-center"> (s)</th>
                  <th style={{width: '8%'}} className="text-center"></th>
                  <th style={{width: '10%'}} className="text-center"></th>
                  <th style={{width: '10%'}} className="text-center"></th>
                  <th style={{width: '12%'}} className="text-center"></th>
                  <th style={{width: '12%'}} className="text-center"></th>
                  <th style={{width: '10%'}} className="text-center"></th>
                </tr>
              </thead>
              <tbody>
                {syllablePoints.map((point, index) => {
                  const duration = point.end - point.start;
                  const convertedValue = testDualAxisChart.yAxisUnit === 'semitone' 
                    ? 12 * Math.log2(point.frequency / 440) + 69 // A4 = 440Hz = 69 semitones
                    : 1200 * Math.log2(point.frequency / 261.63); // cents from C4 (261.63Hz)
                  
                  //       ( )
                  const avgFreq = syllablePoints.reduce((sum, p) => sum + p.frequency, 0) / syllablePoints.length;
                  const freqDeviation = ((point.frequency - avgFreq) / avgFreq * 100);
                  
                  //    
                  const getFrequencyLevel = (freq: number) => {
                    if (freq < 100) return { level: '', color: 'text-info', badge: 'info' };
                    if (freq < 150) return { level: '', color: 'text-primary', badge: 'primary' };
                    if (freq < 200) return { level: '', color: 'text-success', badge: 'success' };
                    if (freq < 300) return { level: '', color: 'text-warning', badge: 'warning' };
                    return { level: '', color: 'text-danger', badge: 'danger' };
                  };
                  
                  //     (:    )
                  const pitchRange = Math.abs(freqDeviation);
                  const getRangeLevel = (range: number) => {
                    if (range < 5) return { level: '', color: 'success' };
                    if (range < 15) return { level: '', color: 'warning' };
                    return { level: '', color: 'danger' };
                  };
                  
                  //    
                  const getSpeedAnalysis = (dur: number) => {
                    if (dur < 0.15) return { speed: '', level: 'fast', color: 'danger' };
                    if (dur < 0.3) return { speed: '', level: 'normal', color: 'success' };
                    if (dur < 0.5) return { speed: '', level: 'slow', color: 'warning' };
                    return { speed: '', level: 'very-slow', color: 'info' };
                  };
                  
                  //     (:   )
                  const getQualityAnalysis = (freq: number, dur: number) => {
                    const stability = freq > 80 && freq < 500 ? 'good' : 'poor';
                    const clarity = dur > 0.1 && dur < 0.8 ? 'clear' : 'unclear';
                    
                    if (stability === 'good' && clarity === 'clear') {
                      return { quality: '', score: '85+', color: 'success' };
                    } else if (stability === 'good') {
                      return { quality: '', score: '70+', color: 'primary' };
                    } else {
                      return { quality: '', score: '<70', color: 'warning' };
                    }
                  };
                  
                  //    
                  const getRelativeLevel = (deviation: number) => {
                    if (Math.abs(deviation) < 5) return { level: '', color: 'success' };
                    if (deviation > 0) return { level: `+${deviation.toFixed(1)}%`, color: 'warning' };
                    return { level: `${deviation.toFixed(1)}%`, color: 'info' };
                  };
                  
                  //    
                  const getSyllableCharacteristics = (syllable: string, freq: number) => {
                    const vowels = ['', '', '', '', '', '', '', '', '', ''];
                    const hasVowel = vowels.some(v => syllable.includes(v));
                    
                    if (freq > 200) return { char: '', type: '' };
                    if (freq < 120) return { char: '', type: '' };
                    return { char: '', type: '' };
                  };
                  
                  const freqLevel = getFrequencyLevel(point.frequency);
                  const rangeLevel = getRangeLevel(pitchRange);
                  const speedAnalysis = getSpeedAnalysis(duration);
                  const qualityAnalysis = getQualityAnalysis(point.frequency, duration);
                  const relativeLevel = getRelativeLevel(freqDeviation);
                  const characteristics = getSyllableCharacteristics(point.syllable, point.frequency);
                  
                  return (
                    <tr 
                      key={index}
                      className={currentPlayingSyllable === index ? 'table-primary' : ''}
                      style={{ cursor: 'pointer' }}
                      onClick={() => handleSyllableClick(index)}
                      title="   "
                    >
                      {/*  */}
                      <td className="text-center fw-bold fs-6">{point.syllable}</td>
                      
                      {/* F0  */}
                      <td className="text-center">
                        <span className={freqLevel.color} style={{fontSize: '0.9em'}}>
                          <strong>{point.frequency.toFixed(1)}</strong>
                        </span>
                      </td>
                      
                      {/* / */}
                      <td className="text-center">
                        <small>{convertedValue.toFixed(1)}</small>
                      </td>
                      
                      {/*   */}
                      <td className="text-center">
                        <small style={{fontSize: '0.75em'}}>
                          {point.start.toFixed(2)}-{point.end.toFixed(2)}
                        </small>
                      </td>
                      
                      {/*   */}
                      <td className="text-center">
                        <small>{duration.toFixed(2)}s</small>
                      </td>
                      
                      {/*   */}
                      <td className="text-center">
                        <span className={`badge bg-${rangeLevel.color} bg-opacity-75`} style={{fontSize: '0.7em'}}>
                          {rangeLevel.level}
                        </span>
                        <br />
                        <small className="text-muted" style={{fontSize: '0.65em'}}>
                          {pitchRange.toFixed(1)}%
                        </small>
                      </td>
                      
                      {/*   */}
                      <td className="text-center">
                        <span className={`badge bg-${speedAnalysis.color} bg-opacity-75`} style={{fontSize: '0.7em'}}>
                          {speedAnalysis.speed}
                        </span>
                        <br />
                        <small className="text-muted" style={{fontSize: '0.65em'}}>
                          {(1/duration).toFixed(1)} syl/s
                        </small>
                      </td>
                      
                      {/*   */}
                      <td className="text-center">
                        <span className={`badge bg-${qualityAnalysis.color} bg-opacity-75`} style={{fontSize: '0.7em'}}>
                          {qualityAnalysis.quality}
                        </span>
                        <br />
                        <small className="text-muted" style={{fontSize: '0.65em'}}>
                          {qualityAnalysis.score}
                        </small>
                      </td>
                      
                      {/*   */}
                      <td className="text-center">
                        <span className={`badge bg-${relativeLevel.color} bg-opacity-75`} style={{fontSize: '0.7em'}}>
                          {relativeLevel.level}
                        </span>
                        <br />
                        <small className="text-muted" style={{fontSize: '0.65em'}}>
                          vs 
                        </small>
                      </td>
                      
                      {/*  */}
                      <td className="text-center">
                        <small style={{fontSize: '0.75em'}}>
                          <div>{characteristics.char}</div>
                          <div className="text-muted" style={{fontSize: '0.9em'}}>
                            {characteristics.type}
                          </div>
                        </small>
                      </td>
                    </tr>
                  );
                })}
              </tbody>
            </table>
          </div>
          <div className="row mt-2">
            <div className="col-md-8">
              <small className="text-muted">
                <i className="fas fa-info-circle me-1"></i>
                        .
              </small>
            </div>
            <div className="col-md-4 text-end">
              <small className="text-muted">
                 {syllablePoints.length}   
              </small>
            </div>
          </div>
        </div>
      )}

      {/*    */}
      {showDeleteConfirm && (
        <div className="modal fade show d-block" style={{ backgroundColor: 'rgba(0,0,0,0.5)' }}>
          <div className="modal-dialog modal-dialog-centered">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title">
                  <i className="fas fa-exclamation-triangle text-warning me-2"></i>
                    
                </h5>
              </div>
              <div className="modal-body">
                <p className="mb-2">
                     ?
                </p>
                <div className="alert alert-warning">
                  <strong>{(() => {
                    const file = uploadedFiles.find(f => f.file_id === fileToDelete);
                    if (!file) return fileToDelete;
                    const parts = file.file_id.split('_');
                    const learnerName = parts[0] || '';
                    const sentence = file.expected_text || parts[3] || '';
                    return `${learnerName} - ${sentence}`;
                  })()}</strong>
                  <br />
                  <small className="text-muted">: {fileToDelete}.wav</small>
                </div>
                <p className="text-danger small mb-0">
                  <i className="fas fa-warning me-1"></i>
                      . WAV  TextGrid   .
                </p>
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-secondary"
                  onClick={handleDeleteCancel}
                  disabled={loading}
                >
                  
                </button>
                <button
                  type="button"
                  className="btn btn-danger"
                  onClick={handleDeleteConfirm}
                  disabled={loading}
                >
                  <i className="fas fa-trash me-2"></i>
                  {loading ? ' ...' : ''}
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default UploadedFileTestSection;