import React, { useRef, useCallback, useEffect } from 'react';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  LineController,
  Title,
  Tooltip,
  Legend,
  ChartOptions,
  ChartData
} from 'chart.js';
import annotationPlugin from 'chartjs-plugin-annotation';

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  LineController,
  Title,
  Tooltip,
  Legend,
  annotationPlugin
);

interface PitchData {
  time: number;
  frequency: number;
  type: 'reference' | 'live';
}

interface SyllableData {
  label: string;
  start: number;
  end: number;
  frequency?: number;
  semitone?: number;
}

export const usePitchChart = (canvasRef: React.RefObject<HTMLCanvasElement | null>, API_BASE: string = '') => {
  const chartRef = useRef<ChartJS | null>(null);
  const pitchDataRef = useRef<PitchData[]>([]);
  const startTimeRef = useRef<number>(0);
  const realtimeLineRef = useRef<number | null>(null); //     
  const playbackLineRef = useRef<number | null>(null); //     
  const [yAxisUnit, setYAxisUnitInternal] = React.useState<'hz' | 'semitone' | 'qtone'>('hz');

  //   Y   
  const setYAxisUnit = useCallback((newUnit: 'hz' | 'semitone' | 'qtone') => {
    console.log(` usePitchChart: Y     ${newUnit}`);
    setYAxisUnitInternal(newUnit);
  }, []);

  const initChart = useCallback(() => {
    if (!canvasRef || !canvasRef.current) {
      console.warn(' Canvas ref not available');
      return;
    }

    const ctx = canvasRef.current.getContext('2d');
    if (!ctx) {
      console.warn(' Canvas context not available');
      return;
    }

    // Destroy existing chart
    if (chartRef.current) {
      chartRef.current.destroy();
    }

    const data: ChartData<'line'> = {
      labels: [],
      datasets: [
        {
          label: ' ',
          data: [],
          borderColor: 'rgb(255, 159, 64)',
          backgroundColor: 'rgb(255, 159, 64)',
          showLine: false,  //    (  )
          pointRadius: 8,   //    
          pointHoverRadius: 8,  //       (  )
          borderWidth: 0,   //   
          tension: 0
        },
        {
          label: ' ',
          data: [],
          borderColor: 'rgba(34, 197, 94, 1)',  //  
          backgroundColor: 'rgba(34, 197, 94, 0.3)',
          showLine: false,
          pointRadius: 12,  //   
          pointHoverRadius: 15,
          borderWidth: 3,
          pointBorderColor: 'rgba(34, 197, 94, 1)',
          pointBackgroundColor: 'rgba(34, 197, 94, 0.8)'
        }
      ]
    };

    const options: ChartOptions<'line'> = {
      responsive: true,
      maintainAspectRatio: false,
      layout: {
        padding: {
          top: 100,  //        
          right: 20,
          bottom: 20,
          left: 20
        }
      },
      scales: {
        x: {
          type: 'linear',
          title: {
            display: true,
            text: ' ()'
          },
          min: 0,
          max: 10
        },
        y: {
          title: {
            display: true,
            text: 'Semitone ()' // , Y    
          }
          // min, max  -     
        }
      },
      plugins: {
        legend: {
          display: false  //     (  )
        },
        tooltip: {
          mode: 'index',
          intersect: false,
          callbacks: {
            label: function(context) {
              const unit = yAxisUnit === 'qtone' ? 'Q-tone' : 'Semitone';
              return `${context.dataset.label}: ${context.parsed.y.toFixed(1)} ${unit}`;
            }
          }
        },
        annotation: {
          annotations: {}
        }
      },
      interaction: {
        mode: 'nearest',
        axis: 'x',
        intersect: false
      },
      animation: {
        duration: 0
      }
    };

    chartRef.current = new ChartJS(ctx, {
      type: 'line',
      data,
      options
    });
  }, [canvasRef]); // yAxisUnit     

  //   semitone  Q-tone  
  const frequencyToSemitone = (frequency: number, baseFrequency: number = 200): number => {
    if (frequency <= 0 || baseFrequency <= 0) return 0;
    return 12 * Math.log2(frequency / baseFrequency);
  };

  const frequencyToQtone = (frequency: number, baseFrequency: number = 200): number => {
    if (frequency <= 0 || baseFrequency <= 0) return 0;
    // Q-tone = Quarter-tone = 1/4 semitone = 24 * log2(f/f0)
    // 1 semitone = 2 Q-tones
    return 24 * Math.log2(frequency / baseFrequency);
  };

  const convertFrequency = useCallback((frequency: number): number => {
    if (yAxisUnit === 'hz') {
      return frequency; // Hz   
    }
    const result = yAxisUnit === 'qtone' ? frequencyToQtone(frequency) : frequencyToSemitone(frequency);
    console.log(` : ${frequency.toFixed(1)}Hz  ${result.toFixed(2)} ${yAxisUnit} (=${yAxisUnit === 'qtone' ? 'Q-tone' : 'Semitone'})`);
    return result;
  }, [yAxisUnit]);

  //  Y      ( )
  const updateYAxisUnit = useCallback(() => {
    if (!chartRef.current) {
      console.log('    .');
      return;
    }
    
    const chart = chartRef.current;
    const yAxisTitle = yAxisUnit === 'hz' ? 'Frequency (Hz)' : yAxisUnit === 'qtone' ? 'Q-tone' : 'Semitone ()';
    console.log(` Y  : ${yAxisUnit},   ${pitchDataRef.current.length}  ...`);
    
    // Y     
    if (chart.options.scales && chart.options.scales.y) {
      const yScale = chart.options.scales.y as any;
      if (yScale.title) {
        console.log(` Y  : "${yScale.title.text}"  "${yAxisTitle}"`);
        yScale.title.text = yAxisTitle;
      }
      
      // Y  
      const newRange = yAxisUnit === 'hz' 
        ? { min: 50, max: 500 }   // Hz 
        : yAxisUnit === 'qtone' 
        ? { min: -20, max: 30 }   //   (  2)
        : { min: -10, max: 15 };  //  
        
      yScale.min = newRange.min;
      yScale.max = newRange.max;
      console.log(` Y  : ${newRange.min} ~ ${newRange.max} (${yAxisUnit})`);
    }
    
    //     -   
    if (chart.options.plugins?.tooltip?.callbacks) {
      chart.options.plugins.tooltip.callbacks.label = function(context: any) {
        const unit = yAxisUnit === 'hz' ? 'Hz' : yAxisUnit === 'qtone' ? 'Q-tone' : 'Semitone';
        return `${context.dataset.label}: ${context.parsed.y.toFixed(1)} ${unit}`;
      };
      console.log(`   : ${yAxisUnit === 'qtone' ? 'Q-tone' : 'Semitone'}`);
    }
    
    //      -   
    if (chart.options.plugins?.annotation?.annotations) {
      const annotations = chart.options.plugins.annotation.annotations;
      const yAxisScale = chart.options.scales.y as any;
      const chartMax = yAxisScale.max;
      const chartMin = yAxisScale.min;
      
      //      (90% ) 
      const fixedLabelY = chartMin + (chartMax - chartMin) * 0.9;
      
      Object.keys(annotations).forEach(key => {
        if (key.startsWith('label_')) {
          const annotation = annotations[key] as any;
          if (annotation.type === 'label') {
            annotation.yValue = fixedLabelY;
            console.log(`   '${annotation.content}'  : ${fixedLabelY.toFixed(1)}`);
          }
        }
      });
    }
    
    //     Y  
    if (pitchDataRef.current.length > 0) {
      const convertedValues: number[] = [];
      
      chart.data.datasets.forEach((dataset, datasetIndex) => {
        const dataArray = dataset.data as Array<{x: number, y: number}>;
        
        dataArray.forEach((point, pointIndex) => {
          const originalData = pitchDataRef.current.find(data => 
            Math.abs(data.time - point.x) < 0.001
          );
          
          if (originalData && originalData.frequency) {
            const convertedValue = convertFrequency(originalData.frequency);
            point.y = convertedValue;
            convertedValues.push(convertedValue);
          }
        });
      });
      
      //  Y   -     
      if (convertedValues.length > 0) {
        const minValue = Math.min(...convertedValues);
        const maxValue = Math.max(...convertedValues);
        const margin = Math.abs(maxValue - minValue) * 0.2 || 3; //  20%  (  )
        
        if (chart.options.scales && chart.options.scales.y) {
          const yAxisScale = chart.options.scales.y as any;
          yAxisScale.min = Math.floor(minValue - margin);
          yAxisScale.max = Math.ceil(maxValue + margin);
          console.log(`    :`, pitchDataRef.current.slice(0, 3));
          console.log(` Y  : ${yAxisScale.min} ~ ${yAxisScale.max} ( : ${minValue.toFixed(1)} ~ ${maxValue.toFixed(1)})`);
        }
      }
    }
    
    //   
    chart.update('active');
    console.log(`    : ${yAxisUnit} , Y : ${yAxisTitle}`);
  }, [yAxisUnit, convertFrequency]);

  // Y    
  useEffect(() => {
    console.log(` useEffect  - yAxisUnit: ${yAxisUnit}, : ${!!chartRef.current}, : ${pitchDataRef.current.length}`);
    updateYAxisUnit();
  }, [updateYAxisUnit, yAxisUnit]);

  const addPitchData = useCallback((frequency: number, timestamp: number, type: 'reference' | 'live' = 'live') => {
    if (!chartRef.current) return;

    let relativeTime: number;
    
    if (type === 'reference') {
      //        
      relativeTime = timestamp;
    } else {
      //       
      if (startTimeRef.current === 0) {
        startTimeRef.current = timestamp;
      }
      relativeTime = (timestamp - startTimeRef.current) / 1000;
    }
    
    const newData: PitchData = {
      time: relativeTime,
      frequency,
      type
    };

    pitchDataRef.current.push(newData);
    const chart = chartRef.current;

    if (type === 'reference') {
      //     
      const convertedValue = convertFrequency(frequency);
      
      chart.data.datasets[0].data.push({
        x: relativeTime,
        y: convertedValue
      });
    } else {
      //    Y   (x=0 )
      const convertedValue = convertFrequency(frequency);
      
      console.log(`  : ${frequency.toFixed(1)}Hz  ${convertedValue.toFixed(1)} ${yAxisUnit} ()`);
      
      //    dataset[1]  (x=0 )
      chart.data.datasets[1].data = [{
        x: 0, //   x=0 
        y: convertedValue
      }];
      
      //  Y   -     
      const yScale = chart.options.scales?.y as any;
      if (yScale) {
        let needsUpdate = false;
        
        if (convertedValue < yScale.min) {
          yScale.min = Math.floor(convertedValue - 2);
          needsUpdate = true;
        }
        if (convertedValue > yScale.max) {
          yScale.max = Math.ceil(convertedValue + 2);
          needsUpdate = true;
        }
        
        if (needsUpdate) {
          console.log(` Y  : ${yScale.min} ~ ${yScale.max} ( : ${convertedValue.toFixed(1)})`);
        }
      }
      
      //     x   
      if (chart.options.plugins?.annotation?.annotations) {
        (chart.options.plugins.annotation.annotations as any).realtimeValue = {
          type: 'line',
          scaleID: 'y',
          value: convertedValue,
          borderColor: 'rgba(34, 197, 94, 1)',
          borderWidth: 2,
          borderDash: [5, 5], //  
          label: {
            display: true,
            position: 'end',
            content: `: ${convertedValue.toFixed(1)}`,
            backgroundColor: 'rgba(34, 197, 94, 0.9)',
            color: 'white',
            font: {
              size: 11
            }
          }
        };
      }
    }

    chart.update('none');
  }, [convertFrequency, startTimeRef]);

  const clearChart = useCallback(() => {
    if (!chartRef.current) return;

    chartRef.current.data.datasets.forEach(dataset => {
      dataset.data = [];
    });
    
    pitchDataRef.current = [];
    startTimeRef.current = 0;
    realtimeLineRef.current = null;

    //    
    if (chartRef.current.options.plugins?.annotation?.annotations) {
      delete (chartRef.current.options.plugins.annotation.annotations as any).realtimeValue;
    }

    //  X  Y  
    if (chartRef.current.options.scales) {
      const xScale = chartRef.current.options.scales.x as any;
      const yScale = chartRef.current.options.scales.y as any;
      
      if (xScale) {
        xScale.min = -0.2; // 0.2  
        xScale.max = 5; //  5 
      }
      if (yScale) {
        yScale.min = yAxisUnit === 'qtone' ? -20 : -10;
        yScale.max = yAxisUnit === 'qtone' ? 30 : 15;
      }
      console.log('  X  Y   ');
    }

    chartRef.current.update();
  }, [yAxisUnit]);

  //     (  )
  const hideRealtimePitchLine = useCallback(() => {
    if (!chartRef.current) return;
    
    //   
    chartRef.current.data.datasets[1].data = [];
    
    //  annotation 
    if (chartRef.current.options.plugins?.annotation?.annotations) {
      delete (chartRef.current.options.plugins.annotation.annotations as any).realtimeValue;
      chartRef.current.update('none');
      console.log('   ');
    }
  }, []);

  //     ( )
  const updateRealtimePitchLine = useCallback((time: number, value: number) => {
    if (!chartRef.current) return;
    
    // Y    
    const convertedValue = convertFrequency(value); // value  Hz 
    
    //   x=0  
    chartRef.current.data.datasets[1].data = [{
      x: 0,
      y: convertedValue
    }];
    
    //  annotation  x   
    if (chartRef.current.options.plugins?.annotation?.annotations) {
      (chartRef.current.options.plugins.annotation.annotations as any).realtimeValue = {
        type: 'line',
        scaleID: 'y',
        value: convertedValue,
        borderColor: 'rgba(34, 197, 94, 1)',
        borderWidth: 2,
        borderDash: [5, 5], //  
        label: {
          display: true,
          position: 'end',
          content: `: ${convertedValue.toFixed(1)}`,
          backgroundColor: 'rgba(34, 197, 94, 0.9)',
          color: 'white',
          font: {
            size: 11
          }
        }
      };
      chartRef.current.update('none');
    }
  }, [convertFrequency]);

  const loadReferenceData = useCallback(async (fileId: string) => {
    try {
      // Load syllable-only pitch data (   )
      const pitchUrl = `${API_BASE}/api/reference_files/${fileId}/pitch?syllable_only=true`;
      //  /syllables API : /pitch?syllable_only=true API   
      
      //   API :    (  )
      console.log(`     : ${fileId}`);
      
      const pitchResponse = await fetch(pitchUrl);
      if (!pitchResponse.ok) {
        throw new Error(`     : ${pitchResponse.status}`);
      }
      
      const pitchData = await pitchResponse.json();
      
      //    
      if (!Array.isArray(pitchData)) {
        console.error('      :', pitchData);
        return;
      }
      
      console.log(`      : ${pitchData.length} `, pitchData);
      
      if (pitchData && pitchData.length > 0) {
        // Clear existing reference data
        if (chartRef.current) {
          chartRef.current.data.datasets[0].data = [];
          //  Clear existing annotations
          if (chartRef.current.options.plugins?.annotation) {
            chartRef.current.options.plugins.annotation.annotations = {};
          }
        }
        
        let maxTime = 0;
        const convertedValues: number[] = [];
        
        //   :    0 
        const firstTime = pitchData.length > 0 ? pitchData[0].time : 0;
        console.log(`  :    ${firstTime.toFixed(2)}s 0s `);
        
        // Add reference data points and collect converted values
        pitchData.forEach((point: {time: number, frequency: number, syllable?: string, start?: number, end?: number}) => {
          //   :     0 
          const normalizedTime = point.time - firstTime;
          addPitchData(point.frequency, normalizedTime, 'reference');
          
          // Y      
          const convertedValue = convertFrequency(point.frequency);
          convertedValues.push(convertedValue);
          
          maxTime = Math.max(maxTime, normalizedTime);
          if (point.syllable) {
            }
        });
        
        //  Y    -    
        if (convertedValues.length > 0 && chartRef.current?.options?.scales?.y) {
          const minValue = Math.min(...convertedValues);
          const maxValue = Math.max(...convertedValues);
          const margin = Math.abs(maxValue - minValue) * 0.2 || 3; //  20%  (  )
          
          const yScale = chartRef.current.options.scales.y as any;
          yScale.min = Math.floor(minValue - margin);
          yScale.max = Math.ceil(maxValue + margin);
          
          console.log(` Y   : ${yScale.min} ~ ${yScale.max} ( : ${minValue.toFixed(1)} ~ ${maxValue.toFixed(1)})`);
        }
        
        //     x   (0.2 )
        if (chartRef.current?.options?.scales?.x && maxTime > 0) {
          const timeMargin = 0.2; // 0.2 
          chartRef.current.options.scales.x.min = -timeMargin; // 0    
          chartRef.current.options.scales.x.max = maxTime + timeMargin;
          console.log(`  X : ${chartRef.current.options.scales.x.min.toFixed(1)}s ~ ${chartRef.current.options.scales.x.max.toFixed(1)}s (: ${timeMargin}s)`);
          chartRef.current.update('none');
        }
        
        //   annotation  (pitchData  )
        const syllableAnnotations = pitchData
          .filter(point => point.syllable && point.start !== undefined && point.end !== undefined)
          .map(point => ({
            syllable: point.syllable,
            label: point.syllable,  //  label   (SyllableData  )
            start: (point.start || point.time) - firstTime,  //  
            end: (point.end || point.time) - firstTime,      //  
            time: point.time - firstTime,
            frequency: point.frequency
          }));
        
        if (syllableAnnotations.length > 0) {
          addSyllableAnnotations(syllableAnnotations);
          console.log(`  annotation  : ${syllableAnnotations.length} `);
        }
      }
    } catch (error) {
      console.error('Failed to load reference data:', error);
    }
  }, [addPitchData]);

  //   :    (  )
  const addSyllableAnnotations = useCallback((syllables: SyllableData[]) => {
    if (!chartRef.current || !syllables || syllables.length === 0) {
      console.log(" addSyllableAnnotations: syllables ");
      return;
    }

    const chart = chartRef.current;
    
    //  annotation plugin    
    if (!chart.options.plugins?.annotation) {
      chart.options.plugins = { ...chart.options.plugins, annotation: { annotations: {} } };
    }
    
    chart.options.plugins.annotation.annotations = {};
    
    console.log("    ");
    
    console.log(' Adding annotations for', syllables.length, 'syllables:');
    console.log(' Sample syllables:', syllables.slice(0, 3));
    
    //  Position labels at fixed position (90% from bottom) 
    const yScale = chart.options.scales?.y;
    const chartMax = (yScale?.max as number) || 500;
    const chartMin = (yScale?.min as number) || 50;
    const labelY = chartMin + (chartMax - chartMin) * 0.9; // 90% from bottom ( )
    
    console.log(" Chart Y :", chartMin, "~", chartMax, "labelY:", labelY);
    
    syllables.forEach((syl, index) => {
      const sylStart = syl.start;
      const sylEnd = syl.end;
      const sylLabel = syl.label;
      
      console.log(`  ${index}: ${sylLabel} (${sylStart.toFixed(3)}s - ${sylEnd.toFixed(3)}s)`);
      
      //     
      if (index === 0) {
        chart.options.plugins.annotation.annotations[`start_${index}`] = {
          type: 'line',
          xMin: sylStart,
          xMax: sylStart,
          borderColor: 'rgba(255, 99, 132, 0.8)',
          borderWidth: 3,
          borderDash: [6, 3]
        };
      }
      
      //    (  )
      chart.options.plugins.annotation.annotations[`end_${index}`] = {
        type: 'line',
        xMin: sylEnd,
        xMax: sylEnd,
        borderColor: 'rgba(255, 99, 132, 0.8)',
        borderWidth: 3,
        borderDash: [6, 3]
      };
      
      //      (Y     )
      const midTime = (sylStart + sylEnd) / 2;
      chart.options.plugins.annotation.annotations[`label_${index}`] = {
        type: 'label',
        xValue: midTime,
        yValue: null,  //  yValue     
        position: {
          x: 'center',
          y: 'start'  //    
        },
        yAdjust: -90,  //       (-90px  )
        content: sylLabel,
        backgroundColor: 'rgba(138, 43, 226, 0.95)',  //   ( )
        borderColor: 'rgba(138, 43, 226, 1)',
        borderWidth: 3,  //  
        borderRadius: 8,  //   
        font: {
          size: 16,  //   
          family: 'Noto Sans KR, -apple-system, sans-serif',
          weight: 'bold'
        },
        color: 'white',
        padding: {
          x: 12,  //  
          y: 6
        },
        //      
        shadow: {
          color: 'rgba(0, 0, 0, 0.3)',
          blur: 4,
          offsetX: 0,
          offsetY: 2
        }
      };
    });
    
    //     annotation 
    try {
      chart.update('none');
      console.log(" Syllable annotations added and chart updated!");
      console.log("  annotations :", Object.keys(chart.options.plugins.annotation.annotations).length);
    } catch (error) {
      console.error(" Chart update :", error);
    }
  }, []);

  const resetForNewRecording = useCallback(() => {
    if (!chartRef.current) return;

    // Clear only live data, keep reference data
    chartRef.current.data.datasets[1].data = [];
    startTimeRef.current = 0;
    
    // Filter pitch data to keep only reference data
    pitchDataRef.current = pitchDataRef.current.filter(data => data.type === 'reference');
    
    chartRef.current.update();
  }, []);

  //  Y   ( )
  const adjustPitch = useCallback((direction: 'up' | 'down') => {
    if (!chartRef.current) return;

    const yScale = chartRef.current.options.scales?.y;
    if (!yScale || typeof yScale.min !== 'number' || typeof yScale.max !== 'number') return;

    const range = yScale.max - yScale.min;
    const step = range * 0.1; // 10% 

    if (direction === 'up') {
      yScale.min += step;
      yScale.max += step;
    } else {
      yScale.min -= step;
      yScale.max -= step;
    }

    chartRef.current.update('none');
    console.log(`  ${direction === 'up' ? '' : ''} :`, {
      min: yScale.min.toFixed(1),
      max: yScale.max.toFixed(1)
    });
  }, []);

  //     -     
  const resetPitch = useCallback(() => {
    if (!chartRef.current) return;

    const yScale = chartRef.current.options.scales?.y;
    if (!yScale) return;

    //      
    if (pitchDataRef.current.length > 0) {
      const convertedValues = pitchDataRef.current.map(data => 
        yAxisUnit === 'qtone' ? frequencyToQtone(data.frequency) : frequencyToSemitone(data.frequency)
      );
      const minValue = Math.min(...convertedValues);
      const maxValue = Math.max(...convertedValues);
      const margin = Math.abs(maxValue - minValue) * 0.2 || 3; //  20%  (  )
      
      yScale.min = Math.floor(minValue - margin);
      yScale.max = Math.ceil(maxValue + margin);
      console.log(`    : ${yScale.min} ~ ${yScale.max}`);
    } else {
      //     
      const defaultRange = yAxisUnit === 'qtone' 
        ? { min: -20, max: 30 }  // Q-tone  
        : { min: -10, max: 15 }; // Semitone  
      yScale.min = defaultRange.min;
      yScale.max = defaultRange.max;
      console.log(`    : ${yScale.min} ~ ${yScale.max}`);
    }

    chartRef.current.update('none');
  }, [yAxisUnit, frequencyToQtone, frequencyToSemitone]);

  //    
  const resetView = useCallback(() => {
    if (!chartRef.current) return;

    const xScale = chartRef.current.options.scales?.x;
    if (!xScale) return;

    xScale.min = 0;
    xScale.max = 10;

    chartRef.current.update('none');
    console.log('   : 0-10');
  }, []);

  //  Y   (/  )
  const updateRange = useCallback((min: number, max: number) => {
    if (!chartRef.current) return;

    const yScale = chartRef.current.options.scales?.y;
    if (!yScale) return;

    yScale.min = min;
    yScale.max = max;

    chartRef.current.update('none');
    console.log(` Y  : ${min} ~ ${max} (${yAxisUnit})`);
  }, [yAxisUnit]);

  //         
  const updatePlaybackProgress = useCallback((currentTime: number) => {
    if (!chartRef.current) return;

    const chart = chartRef.current;
    
    //     
    if (chart.options.plugins?.annotation?.annotations) {
      delete chart.options.plugins.annotation.annotations['playback_progress'];
    }

    playbackLineRef.current = currentTime;

    //      ( )
    chart.options.plugins.annotation.annotations['playback_progress'] = {
      type: 'line',
      xMin: currentTime,
      xMax: currentTime,
      borderColor: 'rgba(255, 0, 0, 0.8)',  //  
      borderWidth: 4,
      borderDash: [4, 2],
      label: {
        content: ' ',
        position: 'start',
        backgroundColor: 'rgba(255, 0, 0, 0.9)',
        borderColor: 'rgba(255, 0, 0, 1)',
        borderRadius: 4,
        borderWidth: 1,
        color: 'white',
        font: {
          size: 12,
          weight: 'bold'
        },
        padding: {
          x: 6,
          y: 3
        }
      }
    };

    //      (   )
    const referenceDataset = chart.data.datasets[0] as any; //   
    if (referenceDataset && referenceDataset.data.length > 0) {
      const originalColor = 'rgb(255, 159, 64)';  //  ( )
      const playedColor = 'rgba(54, 162, 235, 1)'; //  ( )
      
      //     
      const pointColors = referenceDataset.data.map((dataPoint: any) => {
        if (dataPoint && typeof dataPoint.x === 'number') {
          return dataPoint.x <= currentTime ? playedColor : originalColor;
        }
        return originalColor;
      });
      
      //    
      referenceDataset.pointBackgroundColor = pointColors;
      referenceDataset.pointBorderColor = pointColors;
    }

    chart.update('none');
  }, []);

  //        
  const clearPlaybackProgress = useCallback(() => {
    if (!chartRef.current) return;

    const chart = chartRef.current;
    
    //    
    if (chart.options.plugins?.annotation?.annotations) {
      delete chart.options.plugins.annotation.annotations['playback_progress'];
    }

    //       
    const referenceDataset = chart.data.datasets[0] as any; //   
    if (referenceDataset && referenceDataset.data.length > 0) {
      const originalColor = 'rgb(255, 159, 64)';  //  ( )
      
      //     
      referenceDataset.pointBackgroundColor = originalColor;
      referenceDataset.pointBorderColor = originalColor;
    }

    playbackLineRef.current = null;
    chart.update('none');
  }, []);


  useEffect(() => {
    initChart();
    
    return () => {
      if (chartRef.current) {
        chartRef.current.destroy();
      }
    };
  }, [initChart]);



  // Y    
  const setYAxisRange = useCallback((yMin: number, yMax: number) => {
    if (chartRef.current) {
      if (chartRef.current.options?.scales?.y) {
        chartRef.current.options.scales.y.min = yMin;
        chartRef.current.options.scales.y.max = yMax;
        chartRef.current.update('none'); //    
        console.log(` Y  : ${yMin.toFixed(1)} ~ ${yMax.toFixed(1)} Hz`);
      }
    }
  }, []);

  // X    
  const setXAxisRange = useCallback((xMin: number, xMax: number) => {
    if (chartRef.current) {
      if (chartRef.current.options?.scales?.x) {
        chartRef.current.options.scales.x.min = xMin;
        chartRef.current.options.scales.x.max = xMax;
        chartRef.current.update('none'); //    
        console.log(` X  : ${xMin.toFixed(2)} ~ ${xMax.toFixed(2)} `);
      }
    }
  }, []);

  return {
    addPitchData,
    clearChart,
    loadReferenceData,
    addSyllableAnnotations,
    adjustPitch,
    resetView,
    setYAxisRange,
    setXAxisRange,
    updateRealtimePitchLine,
    hideRealtimePitchLine,
    updateRange,
    updatePlaybackProgress,  //      
    clearPlaybackProgress,   //       
    setYAxisUnit, // Y    
    yAxisUnit    //  Y  
  };
};