import React, { useState, useEffect, useCallback } from 'react';

interface SurveyData {
  [key: string]: string | string[];
}

interface SurveyFormProps {
  onSubmit?: (data: SurveyData) => void;
  className?: string;
}

const SurveyForm: React.FC<SurveyFormProps> = ({ onSubmit, className = '' }) => {
  const [formData, setFormData] = useState<SurveyData>({});
  const [isSubmitted, setIsSubmitted] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [showLossAge, setShowLossAge] = useState(false);

  //    (draft) 
  const saveDraft = useCallback(() => {
    try {
      localStorage.setItem('tonebridge_survey_draft', JSON.stringify(formData));
      console.log('   ');
    } catch (e) {
      console.warn(' :', e);
    }
  }, [formData]);

  //    
  useEffect(() => {
    const timer = setTimeout(saveDraft, 2000);
    return () => clearTimeout(timer);
  }, [formData, saveDraft]);

  //     ( )
  useEffect(() => {
    try {
      const draft = localStorage.getItem('tonebridge_survey_draft');
      if (draft) {
        const draftData = JSON.parse(draft);
        setFormData(draftData);
        console.log('    ');
      }
    } catch (e) {
      console.warn('   :', e);
    }
  }, []);

  //     
  const handleInputChange = (name: string, value: string | string[]) => {
    setFormData(prev => ({ ...prev, [name]: value }));

    //    /
    if (name === 'hearing_loss_time') {
      setShowLossAge(value === 'postlingual');
    }
  };

  //     ( 3)
  const handleCheckboxChange = (name: string, value: string, checked: boolean) => {
    const currentValues = (formData[name] as string[]) || [];
    
    if (checked) {
      if (currentValues.length >= 3) {
        alert(' 3   .');
        return;
      }
      handleInputChange(name, [...currentValues, value]);
    } else {
      handleInputChange(name, currentValues.filter(v => v !== value));
    }
  };

  //    
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsLoading(true);

    try {
      //  
      const submitData = {
        ...formData,
        submitted_at: new Date().toISOString(),
        user_agent: navigator.userAgent,
        language: navigator.language
      };

      // API 
      const response = await fetch('/api/save_survey', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(submitData)
      });

      if (response.ok) {
        setIsSubmitted(true);
        //   
        localStorage.removeItem('tonebridge_survey_draft');
        //  
        window.scrollTo({ top: 0, behavior: 'smooth' });
        onSubmit?.(submitData);
        console.log('   ');
      } else {
        throw new Error('  .');
      }
    } catch (error) {
      console.error('  :', error);
      alert('    .   .');
    } finally {
      setIsLoading(false);
    }
  };

  //      
  const RangeInput: React.FC<{ 
    name: string; 
    label: string; 
    min: number; 
    max: number; 
    step?: number;
    value?: number;
  }> = ({ name, label, min, max, step = 1, value = 5 }) => {
    const currentValue = (formData[name] as string) || value.toString();
    
    return (
      <div className="mb-3">
        <label className="form-label">{label}</label>
        <input
          type="range"
          className="form-range"
          name={name}
          min={min}
          max={max}
          step={step}
          value={currentValue}
          onChange={(e) => handleInputChange(name, e.target.value)}
        />
        <div className="text-center mt-1 small text-muted">
           : {currentValue}
        </div>
      </div>
    );
  };

  if (isSubmitted) {
    return (
      <div className="alert alert-success text-center" id="thankYouMessage">
        <h4>   !</h4>
        <p>   ToneBridge    .</p>
        <hr />
        <p className="mb-0">
          <small>    ,   .</small>
        </p>
      </div>
    );
  }

  return (
    <div className={`survey-section ${className}`}>
      <div className="card">
        <div className="card-header">
          <h5 className="mb-0">
            <i className="fas fa-clipboard-list me-2"></i>
            ToneBridge  
          </h5>
        </div>
        <div className="card-body">
          <form onSubmit={handleSubmit} noValidate>
            {/*   */}
            <div className="row mb-4">
              <div className="col-md-6">
                <label className="form-label"></label>
                <input
                  type="number"
                  className="form-control"
                  name="age"
                  min="18"
                  max="100"
                  value={(formData.age as string) || ''}
                  onChange={(e) => handleInputChange('age', e.target.value)}
                  required
                />
              </div>
              <div className="col-md-6">
                <label className="form-label"></label>
                <select
                  className="form-select"
                  name="gender"
                  value={(formData.gender as string) || ''}
                  onChange={(e) => handleInputChange('gender', e.target.value)}
                  required
                >
                  <option value=""></option>
                  <option value="male"></option>
                  <option value="female"></option>
                  <option value="other"></option>
                </select>
              </div>
            </div>

            {/*    */}
            <div className="mb-4">
              <label className="form-label">  </label>
              <div className="form-check">
                <input
                  className="form-check-input"
                  type="radio"
                  name="hearing_loss_time"
                  value="prelingual"
                  checked={(formData.hearing_loss_time as string) === 'prelingual'}
                  onChange={(e) => handleInputChange('hearing_loss_time', e.target.value)}
                />
                <label className="form-check-label">
                     (3 )
                </label>
              </div>
              <div className="form-check">
                <input
                  className="form-check-input"
                  type="radio"
                  name="hearing_loss_time"
                  value="postlingual"
                  checked={(formData.hearing_loss_time as string) === 'postlingual'}
                  onChange={(e) => handleInputChange('hearing_loss_time', e.target.value)}
                />
                <label className="form-check-label">
                     (3 )
                </label>
              </div>
            </div>

            {/*  :    */}
            {showLossAge && (
              <div className="mb-4">
                <label className="form-label">  </label>
                <input
                  type="number"
                  className="form-control"
                  name="loss_age"
                  min="3"
                  max="100"
                  value={(formData.loss_age as string) || ''}
                  onChange={(e) => handleInputChange('loss_age', e.target.value)}
                  required={showLossAge}
                />
              </div>
            )}

            {/*   */}
            <div className="mb-4">
              <label className="form-label"> </label>
              <div className="form-check">
                <input
                  className="form-check-input"
                  type="radio"
                  name="korean_background"
                  value="native"
                  checked={(formData.korean_background as string) === 'native'}
                  onChange={(e) => handleInputChange('korean_background', e.target.value)}
                />
                <label className="form-check-label"></label>
              </div>
              <div className="form-check">
                <input
                  className="form-check-input"
                  type="radio"
                  name="korean_background"
                  value="second_language"
                  checked={(formData.korean_background as string) === 'second_language'}
                  onChange={(e) => handleInputChange('korean_background', e.target.value)}
                />
                <label className="form-check-label">2</label>
              </div>
            </div>

            {/*   ( 3) */}
            <div className="mb-4">
              <label className="form-label">   ( 3)</label>
              {[
                { value: 'pitch', label: ' ' },
                { value: 'rhythm', label: '  ' },
                { value: 'pronunciation', label: ' ' },
                { value: 'intonation', label: ' ' },
                { value: 'volume', label: ' ' },
                { value: 'clarity', label: '' }
              ].map(({ value, label }) => (
                <div key={value} className="form-check">
                  <input
                    className="form-check-input"
                    type="checkbox"
                    name="improvement_areas"
                    value={value}
                    checked={((formData.improvement_areas as string[]) || []).includes(value)}
                    onChange={(e) => handleCheckboxChange('improvement_areas', value, e.target.checked)}
                  />
                  <label className="form-check-label">{label}</label>
                </div>
              ))}
            </div>

            {/*    */}
            <RangeInput
              name="satisfaction"
              label="ToneBridge   (1-10)"
              min={1}
              max={10}
              value={5}
            />

            <RangeInput
              name="ease_of_use"
              label="  (1-10)"
              min={1}
              max={10}
              value={5}
            />

            {/*   */}
            <div className="text-center">
              <button
                type="submit"
                className={`btn btn-primary btn-lg ${isLoading ? 'loading' : ''}`}
                disabled={isLoading}
              >
                {isLoading ? ' ...' : ' '}
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default SurveyForm;